.profile__edit-avatar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 120px;
  height: 120px;
  border-style: none;
  border-radius: 50%;
  opacity: 0;
  z-index: 1;
  background: rgba(0, 0, 0, 0.8);
}

.profile__edit-avatar:hover {
    opacity: 1;
    transition: 0.5s ease-in-out;
  }
  
  @media screen and (max-width: 440px) {
    .profile__edit-avatar {
      width: 120px;
      height: 120px;
    }
  }